.boxSigno{
    background-color: #856957;
    color: #fff;
    margin: 10px 10px 0 10px;
    padding: 5px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    border: 1px solid #734136;
    width: 150px;
    height: 200px;
}

.imagemSigno {
    width: 140px;
    height: 100px;
    background-color: #fff;
    object-fit: contain;
    border-radius: 2px;
    border: 1px solid #734136;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 8px;
    flex: 1;
}

.nomeSigno {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 18px;
}

.periodoNascimento {
    font-size: 14px;
}